.App {
  text-align: center;
  background-color: black;
  height: 100%;
  padding: 0 0 0 0;
  margin: 0 0 0 0;
}

/* This gets Google to fall into place */
.social {
	font-size: 1px;
}

/* Set an optional width for your button wrappers */
.social span {
	display: inline-block;
	width: 110px;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: rgb(2, 2, 2);
  height: 150px;
  padding: 20px;
  color: rgb(166, 133, 197);
}

.App-intro {
  font-size: large;
}

.App-universe{
  height: 100%;
  padding: 0 0 0 0;
  margin: 0;
  margin-left: 2%;
  margin-right: 2%;  
  min-height: 800px;  
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.App-description{
  text-align: justify;  
}

.App-description a{
  color: gray;
}

.App-description p{
  color: rgb(93, 93, 93);
}

.App-description-container{
  margin: 30px 60px 30px 60px;  
}

.App-description-container div{
  padding: 20px;
}

.App-author{
  text-align: right;
}
.Star-dust{
    fill: rgb(141, 78, 184);
}
.Star-star{
    fill: rgb(221, 154, 66);
}
.Star-planet{
    fill: rgb(96, 107, 107);
}

.Sun{
    opacity: 0.8    ;
}

.Star-around{
    opacity:0;
}

.Star-star.Star-Gender-nonMale,
.Star-dust.Star-Gender-nonMale,
.Star-planet.Star-Gender-nonMale{
    fill: rgb(255, 255, 255);    
}
.Star-Glow-Gender-nonMale{
    fill: rgb(248, 90, 195);
    opacity: 1;    
}

.Star-label{
    font-size: 8px;
    fill: rgb(187, 187, 187);
    -webkit-transform: rotate(-37deg);
            transform: rotate(-37deg)
}

.Orbit ellipse{
    fill-opacity: 0;
    stroke-width: 0.5px;
    stroke-opacity: 0.5;
    stroke-dasharray: 1;
    stroke: rgb(79, 16, 116);    
}

.Orbit{
    -webkit-transform: rotate(37deg);
            transform: rotate(37deg);
}


.Link{
    stroke-width: 0.3px;
    stroke: gray;
    stroke-opacity: 0.5 ;
}

.Links{
    -webkit-transform: rotate(37deg);
            transform: rotate(37deg);
}

.Link.Link-child{
    stroke: aqua;
}

.Link.Link-partner{
    stroke: red;
}
.SearchBox-input{
    color: rgb(189, 189, 189);
    background-color: black;
    border: 1px solid transparent;
    padding: 0.375rem 0.75rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: 0.25rem;
}

.SearchBox-input:hover{
    /* border: 0.5px solid; */
    border-color: gray;
}
.Star-Tooltip div{
    max-width: 400px;
    margin: 10px;
}


.Universe-Button{
    background-color: transparent;
    border: 1px solid transparent;
    padding: 0.375rem 0.75rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: 0.25rem;
}

.Universe-Button-child{
    color: aqua;
    opacity: 0.4;
}

.Universe-Button-partner{
    color: red;
    opacity: 0.4;
}

.Universe-Button:hover{
    border: 1px solid;
    border-color: gray;
}
.Axis path,
.Axis line {
  stroke: #E0E0E0;
}

.Axis-Band line,
.Axis-Band path,
.Axis-Bottom line,
.Axis-Bottom path {
  stroke-opacity: 0 ;
}


.Axis text {
  font-size: 11px;
  fill: rgb(136, 138, 226);
}

.Axis-Bottom text{
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
  text-anchor: end;
  fill: none;
}


.testing-colors{
    color:  #2a2b83 #ac4a59 #5cb5d8 #61f574 #f17341;
    color: #E8895B #A2DA3D, #EFF4C3, #10101C;
    color: #E8895B #A2DA3D,  #38397C, #10101C;
}


.BarChart-Ability-male{
    fill: rgb(50, 50, 51);
}

.BarChart-Ability-nonMale{
    fill: #E8895B;
}

.BarChart-Occupation-male{
    fill: rgb(50, 50, 51);
}

.BarChart-Occupation-nonMale{
    fill: #E8895B;
}


body {
  font-family: sans-serif;
  height: 100%;
  padding: 0 0 0 0;
  margin: 0 0 0 0;
}

